.hex-grid {
  width: 80vmin;
  height: 80vmin;
  background-color: #F0F0F0;
  border: 1px solid #0a0a0a;
  border-radius: 50%;
  position: relative;
}

.hex:hover {
  opacity: 0.8;
}

.hex {
  display: flex;
  align-items: center;
  justify-content: center;

  border: 1px solid #0a0a0a;
  border-radius: 50%;
  transition: all;
  transition-timing-function: ease;
  transition-duration: 1s;
  position: absolute;
}
